body {
  background: #fafafa;
  display: flex;
  justify-content: center;
  align-items: start;
}
.label-container {
  margin-left: 3.6rem;
  cursor: pointer;
  display: flex;
  justify-content: center;
  align-items: center;
}
.label-container input {
  display: none;
}
.label-container svg {
  overflow: visible;
  margin-left: 1.4rem;
}
.label-container .path {
  fill: none;
  stroke: black;
  stroke-width: 4;
  stroke-linecap: round;
  stroke-linejoin: round;
  transition: stroke-dasharray 0.5s ease, stroke-dashoffset 0.5s ease;
  stroke-dasharray: 241 9999999;
  stroke-dashoffset: 0;
}
.label-container input:checked ~ svg .path {
  stroke-dasharray: 70.50966644 9999999;
  stroke-dashoffset: -262.27233887;
}
.range-box {
  /* level settings ðŸ‘‡ */
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.range-box .title {
  font-size: 1.6rem;
}
.range-box .PB-range-slider {
  -webkit-appearance: none;
  width: 100%;
  height: 4px;
  border-radius: 5px;
  background: #D5DBE1;
  outline: none;
  opacity: 0.7;
  -webkit-transition: 0.2s;
  transition: opacity 0.2s;
}
.range-box .PB-range-slider::-webkit-slider-thumb {
  -webkit-appearance: none;
  appearance: none;
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #000000;
  cursor: pointer;
  transition: 0.3s ease-in-out;
}
.range-box .PB-range-slider::-webkit-slider-thumb:hover {
  transition: 0.3s ease-in-out;
}
.range-box .PB-range-slider::-moz-range-thumb {
  width: 15px;
  height: 15px;
  border-radius: 50%;
  background-color: #000000;
  cursor: pointer;
}
.range-box .PB-range-slider-div {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 1rem;
  border-radius: 6px;
  width: 36rem;
}
.range-box .PB-range-slidervalue {
  font-weight: 600;
}
.select-box {
  display: flex;
  justify-content: space-between;
  align-items: center;
  width: 100%;
}
.select-box .select-wrapper {
  display: flex;
  overflow-x: scroll;
  max-width: 50rem;
}
.select-box .select-wrapper::-webkit-scrollbar {
  height: 1px;
}
.select-box .select-wrapper .font-item {
  white-space: nowrap;
  margin: 0 3px;
  width: 6rem;
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: center;
  border: 1px solid #333;
  cursor: pointer;
}
.select-box .select-wrapper .font-item:hover {
  background-color: orangered;
  color: white;
}
.select-box .select-wrapper .font-item.active {
  background-color: orangered;
  color: white;
}
.select-box .select-wrapper .font-item.layout-item {
  width: 10rem;
  font-family: "Microsoft Yahei";
  font-size: 12px;
}
.dropdown .paste-button {
  position: relative;
  display: block;
}
.dropdown .button {
  background-color: #fff;
  color: #212121;
  padding: 10px 15px;
  font-size: 15px;
  font-weight: bold;
  border: 2px solid transparent;
  border-radius: 15px;
  cursor: pointer;
}
.dropdown .dropdown-content {
  display: none;
  font-size: 13px;
  position: absolute;
  z-index: 1;
  min-width: 200px;
  background-color: #fff;
  border-radius: 0px 15px 15px 15px;
  box-shadow: 0px 8px 16px 0px rgba(0, 0, 0, 0.2);
}
.dropdown .dropdown-content a {
  color: #4CAF50;
  padding: 8px 10px;
  text-decoration: none;
  display: block;
  transition: 0.1s;
}
.dropdown .dropdown-content a:hover {
  background-color: #4CAF50;
  color: #212121;
}
.dropdown .dropdown-content a:focus {
  background-color: #212121;
  color: #4CAF50;
}
.dropdown .dropdown-content #top:hover {
  border-radius: 0px 13px 0px 0px;
}
.dropdown .dropdown-content #bottom:hover {
  border-radius: 0px 0px 13px 13px;
}
.dropdown .paste-button:hover button {
  border-radius: 15px 15px 0px 0px;
}
.dropdown .paste-button:hover .dropdown-content {
  display: block;
}
#loading {
  position: absolute;
  background-color: rgba(0, 0, 0, 0.6);
  width: 100vw;
  height: 100vh;
  overflow: hidden;
}
#loading .text {
  font-family: NotoSansTangsa;
  font-size: 13px;
  color: white;
  position: absolute;
  bottom: 20vh;
  left: 50%;
  transform: translate(-50%, 0);
}
#loading .svg-frame {
  position: absolute;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  width: 300px;
  height: 300px;
  transform-style: preserve-3d;
  display: flex;
  align-items: center;
  justify-content: center;
}
#loading .svg-frame svg {
  position: absolute;
  transition: 0.5s;
  z-index: calc(1 - (0.2 * var(--j)));
  transform-origin: center;
  width: 344px;
  height: 344px;
  fill: none;
}
#loading .svg-frame:hover svg {
  transform: rotate(-80deg) skew(30deg) translateX(calc(45px * var(--i))) translateY(calc(-35px * var(--i)));
}
#loading .svg-frame svg #center {
  transition: 0.5s;
  transform-origin: center;
}
#loading .svg-frame:hover svg #center {
  transform: rotate(-30deg) translateX(45px) translateY(-3px);
}
#loading #out2 {
  animation: rotate16 7s ease-in-out infinite alternate;
  transform-origin: center;
}
#loading #out3 {
  animation: rotate16 3s ease-in-out infinite alternate;
  transform-origin: center;
  stroke: #ff0;
}
#loading #inner3,
#loading #inner1 {
  animation: rotate16 4s ease-in-out infinite alternate;
  transform-origin: center;
}
#loading #center1 {
  fill: #ff0;
  animation: rotate16 2s ease-in-out infinite alternate;
  transform-origin: center;
}
@keyframes rotate16 {
  to {
    transform: rotate(360deg);
  }
}
#root .App {
  display: flex;
  flex-direction: column;
  align-items: center;
}
.upload-image {
  width: 70rem;
  display: flex;
  align-items: center;
  justify-content: center;
  background-color: #fff;
  padding-top: 2rem;
}
.upload-image .upload-component {
  display: flex;
  align-items: center;
  justify-content: center;
}
.upload-image .upload-component .select-image {
  width: 0.1px;
  height: 0.1px;
  opacity: 0;
  overflow: hidden;
  position: absolute;
  z-index: -1;
}
.upload-image .upload-component .image-label {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 64rem;
  cursor: pointer;
  min-height: 20rem;
  font-size: 1.6rem;
  color: #ccc;
  background-color: white;
  border: 2px dashed #ccc;
}
.upload-image .upload-component .image-label:hover {
  border-color: #333;
  color: #333;
}
.upload-image .display-component img {
  width: 64rem;
  min-height: 30rem;
}
.download-image {
  background-color: #ef4444;
  color: white;
  padding: 1.2rem 0;
  width: 100%;
  border-radius: 2.4rem;
  text-align: center;
  margin: 2rem 0 -2rem 0;
  cursor: pointer;
  font-size: 1.6rem;
}
.infomation {
  font-family: 'LK', sans-serif;
}
.infomation .left .device {
  text-transform: capitalize;
  color: #333;
  font-size: 1.8rem;
  font-weight: 700;
}
.infomation .left .time {
  color: #ccc;
  color: #000;
  opacity: 0.4;
}
.infomation .right .shot {
  display: flex;
  justify-content: start;
  align-items: center;
}
.infomation .right .shot .dot {
  width: 10px;
  height: 10px;
  background-color: orangered;
  border-radius: 50%;
  margin-right: -3px;
}
.infomation .right .shot .text {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.infomation .right .shot .text > div {
  margin-left: 1.2rem;
}
.infomation .gps,
.infomation .time,
.infomation .shot {
  font-family: NotoSansTangsa;
  font-size: 13px;
}
.infomation.Horizontal {
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.infomation.Horizontal .left,
.infomation.Horizontal .right {
  display: flex;
  flex-direction: column;
  justify-content: space-evenly;
  height: 4rem;
}
.infomation.Horizontal .right .shot,
.infomation.Horizontal .right .gps {
  display: flex;
  justify-content: flex-end;
}
.infomation.Horizontal .right .gps {
  color: #000;
  opacity: 0.4;
}
.infomation.Vertical {
  display: flex;
  flex-direction: column;
  justify-content: space-around;
}
.infomation.Vertical .device {
  text-shadow: 2px 2px 5px #ccc;
}
.infomation.Vertical .top {
  display: flex;
  align-items: center;
  justify-content: center;
  height: 2.6rem;
}
.infomation.Vertical .bottom {
  display: flex;
  justify-content: space-around;
  align-items: center;
  height: 2.5rem;
}
.operation {
  width: 70rem;
  padding: 2rem;
  margin-top: 4rem;
  border-radius: 1.2rem;
  box-sizing: border-box;
  background-color: #fff;
}
.operation .option {
  height: 5.2rem;
  width: 100%;
  display: flex;
  justify-content: space-between;
  align-items: center;
}
.operation .option .title {
  font-size: 1.6rem;
}
.operation .option .display-wrapper {
  display: flex;
  justify-content: center;
  align-items: center;
}
.operation .option .display-wrapper label {
  font-size: 1.4rem;
}
